.TodoSearch {
    background-color: aliceblue;
    border-radius: 2px;
    border: 2px solid #202329;
    margin: 0 24px;
    height: 64px;
    width: calc(100% - 62px);
    font-size: 24px;
    text-align: center;
    font-family: 'Open Sans';
    font-weight: 400;
    color: #1e1e1f;
    box-shadow: 0px 5px 50px rgba(32, 35, 41, 0.25);

}

.TodoSearch::placeholder {
    color: #A5A5A5;
    font-family: 'Open Sans' ;
    font-weight: 400;
}

.TodoSearch:focus{
    outline-color: green;
}